"use client";

import React, { useState, useEffect } from 'react';
import { Ticket, TipoTicket, PrioridadeTicket, StatusTicket, CreateTicketRequest } from '@/services/api/ticket.service';
import Input from '@/components/form/input/InputField';
import Select from '@/components/ui/Select';
import Button from '@/components/ui/button/Button';
import Label from '@/components/form/Label';
import { useAuth } from '@/hooks/useAuth';

interface TicketFormProps {
  ticket?: Ticket | null;
  onSubmit: (data: CreateTicketRequest | { status?: StatusTicket; prioridade?: PrioridadeTicket }, files?: File[]) => void;
  onCancel: () => void;
  loading?: boolean;
  editMode?: boolean; // Se true, apenas permite editar status e prioridade (para SUPERADMIN)
}

const tipoOptions = [
  { value: 'PROBLEMA', label: 'Problema' },
  { value: 'AJUDA', label: 'Ajuda' },
  { value: 'MELHORIA', label: 'Melhoria' },
  { value: 'SUGESTAO', label: 'Sugestão' }
];

const prioridadeOptions = [
  { value: 'BAIXA', label: 'Baixa' },
  { value: 'MEDIA', label: 'Média' },
  { value: 'ALTA', label: 'Alta' },
  { value: 'URGENTE', label: 'Urgente' }
];

export default function TicketForm({
  ticket,
  onSubmit,
  onCancel,
  loading = false,
  editMode = false
}: TicketFormProps) {
  const { user } = useAuth();
  const isSuperAdmin = user?.role === 'SUPERADMIN';
  const [formData, setFormData] = useState({
    titulo: '',
    descricao: '',
    tipo: 'PROBLEMA' as TipoTicket,
    prioridade: 'MEDIA' as PrioridadeTicket,
    status: 'ABERTO' as StatusTicket
  });
  const [selectedFiles, setSelectedFiles] = useState<File[]>([]);

  useEffect(() => {
    if (ticket) {
      setFormData({
        titulo: ticket.titulo || '',
        descricao: ticket.descricao || '',
        tipo: ticket.tipo,
        prioridade: ticket.prioridade,
        status: ticket.status
      });
    }
  }, [ticket]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (editMode) {
      // Modo edição: apenas status e prioridade
      onSubmit({
        status: formData.status,
        prioridade: formData.prioridade
      });
    } else {
      // Modo criação: todos os campos
      if (!formData.titulo.trim() || !formData.descricao.trim()) {
        return;
      }
      onSubmit(formData, selectedFiles.length > 0 ? selectedFiles : undefined);
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSelectChange = (name: string) => (selectedOption: any) => {
    setFormData(prev => ({
      ...prev,
      [name]: (selectedOption as any)?.value
    }));
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) {
      const files = Array.from(e.target.files);
      if (files.length > 5) {
        alert('Máximo de 5 arquivos permitidos');
        return;
      }
      setSelectedFiles(files);
    }
  };

  const removeFile = (index: number) => {
    setSelectedFiles(prev => prev.filter((_, i) => i !== index));
  };

  const statusOptions = [
    { value: 'ABERTO', label: 'Aberto' },
    { value: 'EM_ANDAMENTO', label: 'Em Andamento' },
    { value: 'RESOLVIDO', label: 'Resolvido' },
    { value: 'FECHADO', label: 'Fechado' }
  ];

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      {!editMode && (
        <>
          <div>
            <Label htmlFor="titulo">Título *</Label>
            <Input
              id="titulo"
              name="titulo"
              value={formData.titulo}
              onChange={handleInputChange}
              required
              placeholder="Descreva brevemente o problema/sugestão"
              aria-label="Título do ticket"
            />
          </div>

          <div>
            <Label htmlFor="descricao">Descrição *</Label>
            <textarea
              id="descricao"
              name="descricao"
              value={formData.descricao}
              onChange={handleInputChange}
              required
              rows={6}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:text-white"
              placeholder="Descreva detalhadamente o problema, sugestão ou pedido de ajuda"
              aria-label="Descrição do ticket"
            />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="tipo">Tipo *</Label>
              <Select
                id="tipo"
                options={tipoOptions}
                value={tipoOptions.find(opt => opt.value === formData.tipo)}
                onChange={handleSelectChange('tipo')}
                isSearchable={false}
                placeholder="Selecione o tipo"
                aria-label="Tipo do ticket"
              />
            </div>

            <div>
              <Label htmlFor="prioridade">Prioridade *</Label>
              <Select
                id="prioridade"
                options={prioridadeOptions}
                value={prioridadeOptions.find(opt => opt.value === formData.prioridade)}
                onChange={handleSelectChange('prioridade')}
                isSearchable={false}
                placeholder="Selecione a prioridade"
                aria-label="Prioridade do ticket"
              />
            </div>
          </div>
        </>
      )}

      {editMode && (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <Label htmlFor="status">Status *</Label>
            <Select
              id="status"
              options={statusOptions}
              value={statusOptions.find(opt => opt.value === formData.status)}
              onChange={handleSelectChange('status')}
              isSearchable={false}
              placeholder="Selecione o status"
              aria-label="Status do ticket"
            />
          </div>

          <div>
            <Label htmlFor="prioridade">Prioridade *</Label>
            <Select
              id="prioridade"
              options={prioridadeOptions}
              value={prioridadeOptions.find(opt => opt.value === formData.prioridade)}
              onChange={handleSelectChange('prioridade')}
              isSearchable={false}
              placeholder="Selecione a prioridade"
              aria-label="Prioridade do ticket"
            />
          </div>
        </div>
      )}

      {!editMode && (
      <div>
        <Label htmlFor="anexos">Anexos (opcional)</Label>
        <input
          type="file"
          id="anexos"
          name="anexos"
          multiple
          onChange={handleFileChange}
          accept="image/*,application/pdf,.doc,.docx,.xls,.xlsx,.txt,.csv"
          className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-blue-500 dark:bg-gray-700 dark:text-white"
          aria-label="Selecionar anexos"
        />
        <p className="text-xs text-gray-500 dark:text-gray-400 mt-1">
          Máximo 5 arquivos, 10MB cada. Formatos: imagens, PDFs, documentos do Office, textos.
        </p>
        
        {selectedFiles.length > 0 && (
          <div className="mt-2 space-y-1">
            {selectedFiles.map((file, index) => (
              <div key={index} className="flex items-center justify-between text-sm bg-gray-50 dark:bg-gray-800 p-2 rounded">
                <span className="text-gray-700 dark:text-gray-300 truncate">
                  {file.name} ({(file.size / 1024 / 1024).toFixed(2)} MB)
                </span>
                <button
                  type="button"
                  onClick={() => removeFile(index)}
                  className="text-red-600 hover:text-red-700 dark:text-red-400 dark:hover:text-red-300 ml-2"
                  aria-label={`Remover ${file.name}`}
                >
                  ✕
                </button>
              </div>
            ))}
          </div>
        )}
      </div>
      )}

      <div className="flex justify-end gap-3 pt-4 border-t border-gray-200 dark:border-gray-700">
        <Button
          type="button"
          variant="outline"
          onClick={onCancel}
          disabled={loading}
          aria-label="Cancelar"
        >
          Cancelar
        </Button>
        <Button
          type="submit"
          variant="primary"
          disabled={loading || (editMode ? false : (!formData.titulo.trim() || !formData.descricao.trim()))}
          aria-label={ticket ? 'Atualizar ticket' : 'Criar ticket'}
        >
          {loading ? 'Salvando...' : editMode ? 'Atualizar Status' : (ticket ? 'Atualizar' : 'Criar Ticket')}
        </Button>
      </div>
    </form>
  );
}

