"use client";

import React from 'react';
import { Ticket } from '@/services/api/ticket.service';
import { formatDate } from '@/utils/format';
import ButtonCard from '@/components/ui/button/ButtonCard';
import { useAuth } from '@/hooks/useAuth';

interface TicketCardProps {
  ticket: Ticket;
  onView: () => void;
  onEdit?: () => void;
  onDelete?: () => void;
}

function getStatusColor(status: string): string {
  switch (status) {
    case 'ABERTO':
      return 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-400';
    case 'EM_ANDAMENTO':
      return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-400';
    case 'RESOLVIDO':
      return 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400';
    case 'FECHADO':
      return 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-400';
    default:
      return 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-400';
  }
}

function getStatusText(status: string): string {
  switch (status) {
    case 'ABERTO':
      return 'Aberto';
    case 'EM_ANDAMENTO':
      return 'Em Andamento';
    case 'RESOLVIDO':
      return 'Resolvido';
    case 'FECHADO':
      return 'Fechado';
    default:
      return status;
  }
}

function getTipoLabel(tipo: string): string {
  switch (tipo) {
    case 'PROBLEMA':
      return 'Problema';
    case 'AJUDA':
      return 'Ajuda';
    case 'MELHORIA':
      return 'Melhoria';
    case 'SUGESTAO':
      return 'Sugestão';
    default:
      return tipo;
  }
}

function getPrioridadeColor(prioridade: string): string {
  switch (prioridade) {
    case 'BAIXA':
      return 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-400';
    case 'MEDIA':
      return 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-400';
    case 'ALTA':
      return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-400';
    case 'URGENTE':
      return 'bg-red-100 text-red-800 dark:bg-red-900/20 dark:text-red-400';
    default:
      return 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-400';
  }
}

function getPrioridadeText(prioridade: string): string {
  switch (prioridade) {
    case 'BAIXA':
      return 'Baixa';
    case 'MEDIA':
      return 'Média';
    case 'ALTA':
      return 'Alta';
    case 'URGENTE':
      return 'Urgente';
    default:
      return prioridade;
  }
}

export default function TicketCard({
  ticket,
  onView,
  onEdit,
  onDelete
}: TicketCardProps) {
  const { user } = useAuth();
  const isSuperAdmin = user?.role === 'SUPERADMIN';

  return (
    <div className="bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded-lg p-5 hover:shadow-lg transition-shadow duration-200">
      {/* Header */}
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
            {ticket.titulo}
          </h3>
          <div className="flex items-center gap-2 mb-2 flex-wrap">
            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(ticket.status)}`}>
              {getStatusText(ticket.status)}
            </span>
            <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getPrioridadeColor(ticket.prioridade)}`}>
              {getPrioridadeText(ticket.prioridade)}
            </span>
            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-400">
              {getTipoLabel(ticket.tipo)}
            </span>
          </div>
          
          {/* Descrição */}
          <p className="text-sm text-gray-600 dark:text-gray-400 line-clamp-2 mb-3">
            {ticket.descricao}
          </p>

          {/* Informações */}
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-2 text-sm mb-3">
            <div>
              <span className="text-gray-500 dark:text-gray-400">Criado por: </span>
              <span className="font-medium text-gray-900 dark:text-white">
                {ticket.criador?.nome || 'N/A'}
              </span>
            </div>
            {isSuperAdmin && ticket.empresa && (
              <div>
                <span className="text-gray-500 dark:text-gray-400">Empresa: </span>
                <span className="font-medium text-gray-900 dark:text-white">
                  {ticket.empresa.nome}
                </span>
              </div>
            )}
            <div>
              <span className="text-gray-500 dark:text-gray-400">Data: </span>
              <span className="font-medium text-gray-900 dark:text-white">
                {formatDate(ticket.createdAt)}
              </span>
            </div>
            {ticket._count && (
              <div>
                <span className="text-gray-500 dark:text-gray-400">Mensagens: </span>
                <span className="font-medium text-gray-900 dark:text-white">
                  {ticket._count.mensagens}
                </span>
              </div>
            )}
          </div>
        </div>
        
        <div className="flex gap-2 ml-4">
          <ButtonCard
            variant="secondary"
            size="xs"
            onClick={onView}
            title="Visualizar Ticket"
            icon="eye"
          />
          {isSuperAdmin && onEdit && (
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onEdit}
              title="Editar Ticket"
              icon="edit"
            />
          )}
          {onDelete && (
            <ButtonCard
              variant="secondary"
              size="xs"
              onClick={onDelete}
              title="Excluir Ticket"
              icon="delete"
            />
          )}
        </div>
      </div>
    </div>
  );
}
