'use client';

import React, { useState, useEffect, useCallback } from 'react';
import { useRouter } from 'next/navigation';
import { toast } from 'react-hot-toast';
import { Card } from '@/components/ui/card';
import Input from '@/components/form/input/InputField';
import Select, { SelectOption } from '@/components/ui/Select';
import Button from '@/components/ui/button/Button';
import Pagination from '@/components/ui/Pagination';
import Label from '@/components/form/Label';
import { Modal } from '@/components/ui/modal';
import { services } from '@/services/api';
import type { Ticket, StatusTicket, TipoTicket, PrioridadeTicket } from '@/services/api/ticket.service';
import TicketCard from '@/components/tickets/TicketCard';
import TicketForm from '@/components/tickets/TicketForm';
import { PlusIcon } from '@/icons';
import { useAuth } from '@/hooks/useAuth';

type FilterOptions = {
  status?: StatusTicket;
  tipo?: TipoTicket;
  prioridade?: PrioridadeTicket;
  empresaId?: number;
};

export default function AdminTicketsPage() {
  const router = useRouter();
  const { user } = useAuth();
  const [tickets, setTickets] = useState<Ticket[]>([]);
  const [empresas, setEmpresas] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [itemsPerPage, setItemsPerPage] = useState(20);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedTicket, setSelectedTicket] = useState<Ticket | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    loadEmpresas();
  }, []);

  const loadEmpresas = async () => {
    try {
      const response = await services.empresa.list({ perPage: 1000 });
      setEmpresas(response.data || []);
    } catch (error) {
      console.error('Erro ao carregar empresas:', error);
    }
  };

  const loadData = useCallback(async () => {
    try {
      setLoading(true);
      const params: any = {
        page,
        perPage: itemsPerPage,
        search: searchTerm || undefined
      };

      if (filters.status) {
        params.status = filters.status;
      }

      if (filters.tipo) {
        params.tipo = filters.tipo;
      }

      if (filters.prioridade) {
        params.prioridade = filters.prioridade;
      }

      if (filters.empresaId) {
        params.empresaId = filters.empresaId;
      }

      const response = await services.ticket.list(params);
      setTickets(response.data || []);
      setTotalPages(response.pagination?.totalPages || 1);
    } catch (error) {
      console.error('Erro ao carregar tickets:', error);
      toast.error('Erro ao carregar tickets');
    } finally {
      setLoading(false);
    }
  }, [page, filters, itemsPerPage, searchTerm]);

  useEffect(() => {
    loadData();
  }, [loadData]);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1);
  };

  const handleFilterChange = (newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
    setPage(1);
  };

  const handleEditTicket = (ticket: Ticket) => {
    setSelectedTicket(ticket);
    setIsModalOpen(true);
  };

  const handleViewTicket = (ticket: Ticket) => {
    router.push(`/dashboard/admin/tickets/${ticket.id}`);
  };

  const handleDeleteTicket = async (ticket: Ticket) => {
    if (!window.confirm('Tem certeza que deseja excluir este ticket?')) {
      return;
    }

    try {
      await services.ticket.delete(ticket.id);
      toast.success('Ticket excluído com sucesso');
      loadData();
    } catch (error: any) {
      toast.error(error?.response?.data?.error || 'Erro ao excluir ticket');
    }
  };

  const handleSave = async (data: any, files?: File[]) => {
    if (!selectedTicket) return;

    setIsSubmitting(true);
    try {
      await services.ticket.update(selectedTicket.id, {
        status: data.status,
        prioridade: data.prioridade
      });
      toast.success('Ticket atualizado com sucesso');
      setIsModalOpen(false);
      setSelectedTicket(null);
      loadData();
    } catch (error: any) {
      toast.error(error?.response?.data?.error || 'Erro ao atualizar ticket');
    } finally {
      setIsSubmitting(false);
    }
  };

  const statusOptions: SelectOption[] = [
    { value: '', label: 'Todos' },
    { value: 'ABERTO', label: 'Aberto' },
    { value: 'EM_ANDAMENTO', label: 'Em Andamento' },
    { value: 'RESOLVIDO', label: 'Resolvido' },
    { value: 'FECHADO', label: 'Fechado' }
  ];

  const tipoOptions: SelectOption[] = [
    { value: '', label: 'Todos' },
    { value: 'PROBLEMA', label: 'Problema' },
    { value: 'AJUDA', label: 'Ajuda' },
    { value: 'MELHORIA', label: 'Melhoria' },
    { value: 'SUGESTAO', label: 'Sugestão' }
  ];

  const prioridadeOptions: SelectOption[] = [
    { value: '', label: 'Todas' },
    { value: 'BAIXA', label: 'Baixa' },
    { value: 'MEDIA', label: 'Média' },
    { value: 'ALTA', label: 'Alta' },
    { value: 'URGENTE', label: 'Urgente' }
  ];

  const empresaOptions: SelectOption[] = [
    { value: '', label: 'Todas' },
    ...empresas.map(emp => ({
      value: emp.id.toString(),
      label: emp.nome
    }))
  ];

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Gerenciar Tickets
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie todos os tickets do sistema
            </p>
          </div>
        </div>

        <div className="mt-6">
          <div className="flex flex-col sm:flex-row items-center gap-4 mb-6">
            <div className="flex-1 w-full sm:max-w-xs">
              <Input
                type="text"
                placeholder="Buscar tickets..."
                value={searchTerm}
                onChange={handleSearch}
                className="w-full"
                aria-label="Buscar tickets"
              />
            </div>
            <div className="flex flex-wrap gap-3">
              <Select
                placeholder="Status"
                options={statusOptions.filter(opt => opt.value !== '')}
                value={statusOptions.find(opt => opt.value === (filters.status || ''))}
                onChange={(option) => handleFilterChange({ status: (option as any)?.value || undefined })}
                isClearable
                className="whitespace-nowrap min-w-40"
                aria-label="Filtrar por status"
              />
              <Select
                placeholder="Tipo"
                options={tipoOptions.filter(opt => opt.value !== '')}
                value={tipoOptions.find(opt => opt.value === (filters.tipo || ''))}
                onChange={(option) => handleFilterChange({ tipo: (option as any)?.value || undefined })}
                isClearable
                className="whitespace-nowrap min-w-40"
                aria-label="Filtrar por tipo"
              />
              <Select
                placeholder="Prioridade"
                options={prioridadeOptions.filter(opt => opt.value !== '')}
                value={prioridadeOptions.find(opt => opt.value === (filters.prioridade || ''))}
                onChange={(option) => handleFilterChange({ prioridade: (option as any)?.value || undefined })}
                isClearable
                className="whitespace-nowrap min-w-40"
                aria-label="Filtrar por prioridade"
              />
              <Select
                placeholder="Empresa"
                options={empresaOptions.filter(opt => opt.value !== '')}
                value={empresaOptions.find(opt => opt.value === (filters.empresaId?.toString() || ''))}
                onChange={(option) => handleFilterChange({ empresaId: (option as any)?.value ? parseInt((option as any).value) : undefined })}
                isClearable
                className="whitespace-nowrap min-w-40"
                aria-label="Filtrar por empresa"
              />
            </div>
          </div>

          <div className="flex justify-between mb-4 gap-2 flex-wrap">
            <div className='flex items-center gap-0.5'>
              <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
              <Input
                className='max-w-24'
                defaultValue={itemsPerPage}
                max={100}
                min={1}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  const newValue = Number(e.target.value) || 9;
                  setItemsPerPage(newValue);
                  setPage(1);
                }}
                aria-label="Itens por página"
              />
            </div>
            {totalPages > 1 && (
              <Pagination
                currentPage={page}
                totalPages={totalPages}
                onPageChange={setPage}
              />
            )}
          </div>
        </div>
      </div>

      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {tickets.length > 0 ? (
            tickets.map((ticket) => (
              <TicketCard
                key={ticket.id}
                ticket={ticket}
                onView={() => handleViewTicket(ticket)}
                onEdit={() => handleEditTicket(ticket)}
                onDelete={() => handleDeleteTicket(ticket)}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <p className="text-gray-500 dark:text-gray-400">
                Nenhum ticket encontrado.
              </p>
            </div>
          )}
        </div>
      )}

      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      {/* Modal de Editar Ticket */}
      {selectedTicket && (
        <Modal
          isOpen={isModalOpen}
          onClose={() => {
            setIsModalOpen(false);
            setSelectedTicket(null);
          }}
          className="max-w-2xl m-4"
        >
          <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
            <div className="px-2 pr-14">
              <h2 className="text-2xl font-semibold text-gray-900 dark:text-white mb-6">
                Editar Ticket
              </h2>
              <div className="space-y-4">
                <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                  <p className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Título:
                  </p>
                  <p className="text-sm text-gray-900 dark:text-white">
                    {selectedTicket.titulo}
                  </p>
                </div>
                <div className="p-4 bg-gray-50 dark:bg-gray-800 rounded-lg">
                  <p className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Descrição:
                  </p>
                  <p className="text-sm text-gray-900 dark:text-white whitespace-pre-wrap">
                    {selectedTicket.descricao}
                  </p>
                </div>
                <TicketForm
                  ticket={selectedTicket}
                  onSubmit={handleSave}
                  onCancel={() => {
                    setIsModalOpen(false);
                    setSelectedTicket(null);
                  }}
                  loading={isSubmitting}
                  editMode={true}
                />
              </div>
            </div>
          </div>
        </Modal>
      )}
    </div>
  );
}

